import React from 'react';
export const  SearchBar=(props)=>
                {
                    return(<div className='form-group'> 
                       <input type='text' onChange={props.takeinput} className='form-control' placeholder='Type to Search any GIF'/> <br/>
                       <button onClick={props.btclick} className='btn btn-primary'>Search GIF</button>                   
                    </div>);
                }
