import React from 'react';
import { Picture } from './Picture';
export const SearchList =(props)=>
{
    let count=props.imagedata.length;
    const tempJSX=<p>NO Record found :(</p>;
    const stempJSX=(<>
                    <p>List of Records= {count}</p>
                    {props.imagedata.map(ele=><Picture src={ele.images.original.url}/>)}
                    {/* {props.imagedata.map(ele=><img src={ele.images.original.url}></img>)} */}

                    <Picture/>
                    </>
                    );
    let length=0;
    console.log('Props record in List',props.imagedata.length);
    return(
        <div>   
            {props.imagedata.length==0?tempJSX:stempJSX}
        </div>
    );
   
}