import React from 'react';
import {SearchBar} from '../components/SearchBar';
import { SearchList } from '../components/SearchList';

export default class search extends React.Component
{
    //initializes props and states 
    //props are the values coming from the Parent    
    constructor(props)
    {
        super();
    //calling a parent constructor
        this.state={imagedata:[]};
        this.searchtextvalue= '';
    }
    componentDidMount()
    {
        //this.giveMeData();
    }
    takeinput(event)
    {
        let d=event.target.value;
        this.searchtextvalue=d;
        console.log('calling by child :)) ',this.searchtextvalue);
    }
    searchnow()
    {
        console.log('Search btton clicked');
        this.giveMeData(this.searchtextvalue)
    }
    giveMeData(x)
    {
        const url=`https://api.giphy.com/v1/gifs/search?api_key=8HcceAhalQPSxTWG7cAAJPsekJfTuQnE&q=${x}&limit=10`;
        const promise=fetch(url);
        promise.then(response=>
            {
                //console.log('Response is coming',response);
                var p=response.json();
                p.then(result=>
                    {
                        console.log('Data is ',result.data);
                        this.setState({imagedata:result.data});
                        
                     }).catch(err=>
                    {
                        console.log('Error in JSON file');
                    })
               // console.log('RESPONSE');
            }).catch(err=>
            {
                console.log('Error in the url fetching');
            })
    }

    render()
    {

        console.log('RENDER Data is ',this.state.imagedata);
        //console.log(typeof(this.state.imagedata));
        return(
            <div>
                <h1 className='alert-danger text-center'><b>{this.props.title}</b></h1>              
                <SearchBar takeinput={this.takeinput.bind(this)} btclick={this.searchnow.bind(this)} /> 
                {/* writing bind cuz the data is getting lost for each input given */}
                <SearchList imagedata={this.state.imagedata}/>
                <h4 className='alert-info text-right'> <i>{this.props.name}</i></h4>
            </div>
        )
    }
}
